import React, { useState, useEffect } from 'react';
import { useScrollPercentage } from 'react-scroll-percentage';
import './LetterFader.scss';

interface Props {
	options: any;
}

export const LetterFader = (
	{ options }: Props,
) => {
	const [activeGroup, setActiveGroup] = useState<number>(1);

	useEffect(() => {
		if (options?.strings) {
			const countChangeInterval = setInterval(() => {
				let targetGroup = activeGroup + 1;
				if (targetGroup > options?.strings.length) {
					targetGroup = 1;
				}
				setActiveGroup(targetGroup);
			}, 4000);
			return () => {
				clearInterval(countChangeInterval);
			};
		}
		return undefined;
	}, [activeGroup, options]);

	// Scroll Monitor
	const [ref, percentage] = useScrollPercentage({
		/* Optional options */
		threshold: 0,
	});
	const processPercentage = parseFloat(percentage.toPrecision(2));

	const addSpan = (text: any) => {
		if (!text) return '';
		const notes = text.split(' ');
		console.log('notes', notes);
		let letterDelay = 0;
		return notes.map((note: any, index: number) => {
			const adjIndex = index + 1;
			const key = `letter-fader-word-${adjIndex}`;
			return (
				<React.Fragment key={key}>
					<span
						className="letter-fader__word"
					>
						{
							[...note].map((letter: string, letterId: number) => {
								const adjLetterId = letterId + 1;
								const letterKey = `letter-fader-letter-${adjLetterId}`;
								if (!(index === 0 && letterId === 0)) {
									letterDelay += 0.1;
								}
								return (
									<span
										key={letterKey}
										className={`letter-fader__letter ${letter === ' ' ? 'letter-fader__letter-empty' : ''}`}
										style={{
											animationDelay: `${letterDelay}s`,
										}}
									>
										{letter === ' ' ? (<>&nbsp;</>) : letter}
									</span>
								);
							})
						}
						{adjIndex < notes.length ? (
							<span className="letter-fader__letter letter-fader__letter-empty">&nbsp;</span>
						) : null}
					</span>
				</React.Fragment>
			);
		});
	};

	return (
		<div
			ref={ref}
			className={`letter-fader ${processPercentage > 0.2 ? 'is-active' : ''}`}
		>
			{options?.strings?.map((text: any, index: number) => {
				const adjIndex = index + 1;
				const key = `letter-fader-group-${adjIndex}`;
				return (
					<div
						key={key}
						className={`letter-fader__grp ${activeGroup === adjIndex ? 'is-active' : ''}`}
					>
						{addSpan(text)}
					</div>
				);
			})}
		</div>
	);
};

export default LetterFader;
