import React, { useEffect, useState } from 'react';
import { useScrollPercentage } from 'react-scroll-percentage';
import { PerspectiveBlock } from '../../components';
import Typewriter from 'typewriter-effect';
import './Gallery.scss';

interface Props {
	pageProgess: any;
	updatePageProgess: (status: any) => void;
}

const Gallery = ({ pageProgess, updatePageProgess }: Props) => {
	const [ref, percentage] = useScrollPercentage({
		/* Optional options */
		threshold: 0,
	});

	const processPercentage = parseFloat(percentage.toPrecision(2));

	const sectionActive = processPercentage > 0.2;

	useEffect(() => {
		if (sectionActive && pageProgess.gallery !== 'active') {
			if (pageProgess.gallery !== 'active') {
				updatePageProgess({
					...pageProgess,
					gallery: 'active',
				});
			}
		} else if (pageProgess.gallery === 'active') {
			updatePageProgess({
				...pageProgess,
				gallery: '',
			});
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [sectionActive]);

	return (
		<div
			ref={ref}
			className={`sec-gallery ${sectionActive ? 'is-active' : ''}`}
		>
			<div
				className="sec-gallery__inner"
			>
				<h2 className="sec-gallery__title">
					<span
						className="sec-gallery__title-icon"
						style={{
							backgroundImage: 'url(/img/gallery/icon-gallery.svg)',
						}}
					/>
					<small className="sec-gallery__title-tag">EXHIBITION HIGHLIGHTS</small>
					<span className="sec-gallery__title-txt">
						Gallery
						<br />
						<span className="sec-gallery__title-gray">COMING SOON</span>
					</span>
				</h2>
				<div className="sec-gallery__desc">
					<p>
						The Virtual Gallery of the exhibition will be available
						for visiting online herein Mid January.
						<br />
						Stay Tuned!
					</p>
				</div>
				<div className="sec-gallery__gallery">
					<div className="sec-gallery__gallery-item">
						<div className="sec-gallery__gallery-item-body">
							<PerspectiveBlock
								initialPoint={45}
								endPoint={0}
							>
								<div
									className="sec-gallery__gallery-graphic"
									style={{
										backgroundImage: 'url(/img/gallery/img-gallery.jpg)',
									}}
								/>
							</PerspectiveBlock>
							<p className="sec-gallery__item-desc">Exhibition Highlights are coming!</p>
						</div>
					</div>
					<div className="sec-gallery__gallery-item">
						<div className="sec-gallery__gallery-item-body">
							<PerspectiveBlock>
								<div className="sec-gallery__gallery-graphic" />
							</PerspectiveBlock>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

export default Gallery;
