import React, { useEffect, useState } from 'react';
import { useScrollPercentage } from 'react-scroll-percentage';
import './Video.scss';
import { getElementError } from '@testing-library/react';

interface Props {
	pageProgess: any;
	updatePageProgess: (status: string) => void;
}

const Video = ({ pageProgess, updatePageProgess }: Props) => {
	// Scroll Monitor
	const [ref, percentage] = useScrollPercentage({
		/* Optional options */
		threshold: 0,
	});
	const processPercentage = parseFloat(percentage.toPrecision(2));


	const getVideoTransform = () => {
		// Rotate
		const initialRotate = -15;
		const rotateFactor = 1 - (processPercentage * 3);
		let rotateDegree = rotateFactor * initialRotate;
		if (rotateDegree > 0) {
			rotateDegree = 0;
		}
		if (processPercentage > 0.5) {
			rotateDegree = 0;
		}

		// Transform
		const fullScreen = 0.4;
		const fadeOutStartPoint = 0.6;
		const fadeOutEndPoint = 0.75;
		let transformPercent = ((1 - fullScreen) + processPercentage);
		if (processPercentage > fadeOutStartPoint) {
			transformPercent = (fadeOutEndPoint - processPercentage)
				/ (fadeOutEndPoint - fadeOutStartPoint);
			if (transformPercent < 0.65) {
				transformPercent = 0.65;
			}
		}
		if (transformPercent > 1) {
			transformPercent = 1;
		}

		return `scale(${transformPercent}) rotate(${rotateDegree}deg)`;
	};


	const getOverlayOpacity = () => {
		let overlayOpacity = processPercentage * 2;
		if (processPercentage > 0.5) {
			overlayOpacity = 1 - processPercentage * 1.5;
		}
		if (overlayOpacity > 1) {
			overlayOpacity = 1;
		}
		if (overlayOpacity < 0) {
			overlayOpacity = 0;
		}
		return `${overlayOpacity}`;
	};

	useEffect(() => {
		const videoEl: any = document.getElementById('main-video');
		videoEl.play();
	}, []);

	return (
		<>
			<div
				ref={ref}
				className={`sec-video ${pageProgess ? 'is-active' : ''}`}
			>
				<div className="sec-video__inner">
					<div
						className="sec-video__video"
						style={{
							transform: getVideoTransform(),
						}}
					>
						<video
							id="main-video"
							autoPlay
							loop
							muted
							playsInline
						>
							<source src="/videos/video-dummy-bunny.mp4" type="video/mp4" />
						Your browser does not support the video tag.
						</video>
					</div>
				</div>
			</div>
			<div
				className="sec-video__overlay"
				style={{
					opacity: getOverlayOpacity(),
				}}
			/>
			{/* <div className="sec-video__process-info">
				{parseFloat(percentage.toPrecision(2))}
				<p>
					{JSON.stringify(pageProgess)}
				</p>
			</div> */}
		</>
	);
};

export default Video;
